/* Btn-menu 
------------------------------------------------*/
.btn-menu {
  display: none;
  position: relative;
  background: transparent;
  float: right;
  cursor: pointer;
  margin: 38px 0px 35px 30px;
  width: 26px;
  height: 16px;
  margin-right: 15px;
}

.btn-menu span {
  position: absolute;
  width: 25px;
  height: 2px;
  left: 0;
  top: 50%;
  overflow: hidden;
  text-indent: 200%;
}

.btn-menu::before,
.btn-menu::after {
  content: "";
  position: absolute;
  top: 0;
  height: 2px;
  width: 25px;
  left: 0;
  top: 50%;
  -webkit-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
}

.btn-menu::after {
  -webkit-transform: translate3d(0, 7px, 0);
  transform: translate3d(0, 7px, 0);
}

.btn-menu::before {
  -webkit-transform: translate3d(0, -7px, 0);
  transform: translate3d(0, -7px, 0);
}

.btn-menu::before,
.btn-menu::after,
.btn-menu span {
  background-color: #ffbf43;
  transition: all ease 0.238s;
}

.btn-menu.active span {
  opacity: 0;
}

.btn-menu.active::before {
  -webkit-transform: rotate3d(0, 0, 1, 45deg);
  transform: rotate3d(0, 0, 1, 45deg);
}

.btn-menu.active::after {
  -webkit-transform: rotate3d(0, 0, 1, 45deg);
  transform: rotate3d(0, 0, 1, -45deg);
}

/* Btn-Submenu */
