/* Go-top
-------------------------------------------------------------- */
.go-top,
.go-top::before,
.go-top::after {
  transition: all 0.5s ease-in-out;
}

.go-top {
  position: fixed;
  opacity: 0;
  right: -1%;
  bottom: 25px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 1px solid #ffbf43;
  cursor: pointer;
}

.go-top.show {
  bottom: 25px;
  right: 2%;
  opacity: 1;
  z-index: 99;
}

.go-top::before,
.go-top::after {
  position: absolute;
  left: 9px;
  top: 7px;
  content: "";
  width: 1px;
  height: 12px;
  color: #ffbf43;
  z-index: 999;
  background-color: #ffbf43;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.go-top::after {
  position: absolute;
  left: 17px;
  -webkit-transform: rotate(135deg);
  transform: rotate(135deg);
}

.go-top:hover {
  background-color: #ffbf43;
}

.go-top:hover:before,
.go-top:hover:after {
  background-color: #fff;
}

